import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box } from '@mui/material';
import { Link, useLocation } from 'react-router-dom';
import { motion } from 'framer-motion';
import { BriefcaseIcon } from '@heroicons/react/24/outline';

const Navbar: React.FC = () => {
  const location = useLocation();

  return (
    <AppBar
      position="sticky"
      sx={{
        background: 'rgba(40, 28, 16, 0.9)',
        backdropFilter: 'blur(10px)',
        borderBottom: '1px solid rgba(255, 215, 140, 0.2)',
        boxShadow: '0 2px 20px rgba(0, 0, 0, 0.3)',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          background: `repeating-linear-gradient(
            45deg,
            rgba(255, 215, 140, 0.1),
            rgba(255, 215, 140, 0.1) 2px,
            transparent 2px,
            transparent 4px
          )`,
          pointerEvents: 'none'
        }
      }}
    >
      {/* Decorative floating briefcase icon */}
      <motion.div 
        style={{ 
          position: 'absolute', 
          top: '50%', 
          right: '20px',
          opacity: 0.2
        }}
        animate={{ 
          y: [-5, 5, -5],
          rotate: [0, 10, -10, 0]
        }}
        transition={{ 
          duration: 6, 
          repeat: Infinity,
          ease: "easeInOut"
        }}
      >
        <BriefcaseIcon className="h-8 w-8 text-amber-400" />
      </motion.div>

      <Toolbar sx={{ position: 'relative', zIndex: 2 }}>
        <Typography 
          variant="h6" 
          sx={{
            fontWeight: 800,
            flexGrow: 1,
            color: '#ffd78c',
            textShadow: '0 1px 2px rgba(0,0,0,0.3)',
            letterSpacing: '-0.03em'
          }}
        >
          Dhruvil Kotecha
        </Typography>
        
        <Box sx={{ display: 'flex', gap: 1 }}>
          {['Home', 'Experience', 'Academics', 'Projects', 'About Me','Blog'].map((item) => {
            const path = item === 'Home' 
              ? '/' 
              : item === 'Blog'
                ? '/blog/focaccia-llm'  // Special case for Blog
                : `/${item.toLowerCase().replace(' ', '')}`;
            const isActive = location.pathname === path;
            
            return (
              <motion.div
                key={item}
                whileHover={{ y: -2 }}
                whileTap={{ scale: 0.95 }}
              >
                <Button 
                  component={Link} 
                  to={path}
                  sx={{
                    fontWeight: 700,
                    textTransform: 'none',
                    fontSize: '1rem',
                    color: isActive ? '#ffb347' : '#ffd78c',
                    '&:hover': {
                      color: '#ffb347',
                      background: 'rgba(255, 179, 71, 0.1)'
                    },
                    position: 'relative',
                    '&::after': {
                      content: '""',
                      position: 'absolute',
                      bottom: 0,
                      left: 0,
                      width: isActive ? '100%' : '0%',
                      height: '2px',
                      background: 'linear-gradient(90deg, #ffb347, #ffd78c)',
                      transition: 'width 0.3s ease'
                    },
                    '&:hover::after': {
                      width: '100%'
                    }
                  }}
                >
                  {item}
                </Button>
              </motion.div>
            );
          })}
        </Box>
      </Toolbar>
    </AppBar>
  );
}

export default Navbar;