// New VideoPlayer component
const VideoPlayer = ({ src, title, credit }: { 
  src: string; 
  title: string; 
  credit?: { text: string; url: string } 
}) => {
  return (
    <Box sx={{ mb: 3 }}>
      <Typography variant="h6" sx={{ color: '#ffb347', mb: 1 }}>
        {title}
      </Typography>
      {credit && (
        <Typography variant="caption" sx={{ 
          color: 'rgba(255, 215, 140, 0.6)', 
          display: 'block',
          mb: 1,
          fontStyle: 'italic'
        }}>
          <Link 
            href={credit.url} 
            target="_blank" 
            rel="noopener noreferrer"
            sx={{
              color: 'rgba(255, 215, 140, 0.6)',
              textDecoration: 'none',
              '&:hover': {
                color: '#ffb347',
                textDecoration: 'underline'
              }
            }}
          >
            {credit.text}
          </Link>
        </Typography>
      )}
      <Box
        component="video"
        controls
        sx={{
          width: '100%',
          maxWidth: '800px',
          borderRadius: '8px',
          boxShadow: '0 4px 20px rgba(0,0,0,0.3)',
          border: '1px solid rgba(255, 179, 71, 0.3)'
        }}
      >
        <source src={src} type="video/mp4" />
        Your browser does not support the video tag.
      </Box>
    </Box>
  );
};

