const fadeInStagger = {
  hidden: {},
  show: { 
    transition: { 
      staggerChildren: 0.2,
      delayChildren: 0.3
    } 
  }
};

const fadeItem = {
  hidden: { opacity: 0, y: 20 },
  show: { 
    opacity: 1, 
    y: 0,
    transition: {
      duration: 0.6,
      ease: "easeOut"
    }
  }
};

