import React from 'react';
import { Box } from '@mui/material';
import { motion } from 'framer-motion';

const CodingAnimation = () => {
  return (
    <Box sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: 1, // Higher than default but below content
        pointerEvents: 'none',
        display: 'flex',
        justifyContent: 'flex-end',
        alignItems: 'flex-end',
        pr: { xs: '5%', md: '10%' },
        pb: { xs: '5%', md: '10%' }
      }}>
        <motion.svg
          width="300"
          height="300"
          viewBox="0 0 400 400"
          style={{
            opacity: 0.7, // Slightly more visible but still subtle
            filter: 'drop-shadow(0 0 8px rgba(255, 215, 140, 0.3))'
          }}
          animate={{
            y: [0, -15, 0],
            opacity: [0.7, 0.7, 0.7]
          }}
          transition={{
            duration: 8,
            repeat: Infinity,
            ease: "easeInOut"
          }}
        >
          {/* Head */}
          <circle cx="200" cy="120" r="30" fill="none" stroke="#ffd78c" strokeWidth="2.5" strokeOpacity="0.8"/>
          
          {/* Body */}
          <line x1="200" y1="150" x2="200" y2="250" stroke="#ffd78c" strokeWidth="2.5" strokeOpacity="0.8"/>
          
          {/* Arms */}
          <motion.line
            x1="200" y1="180" x2="150" y2="200"
            stroke="#ffb347"
            strokeWidth="2.5"
            strokeOpacity="0.8"
            animate={{
              x2: [150, 160, 150],
              y2: [200, 210, 200]
            }}
            transition={{
              duration: 1,
              repeat: Infinity,
              repeatType: "reverse"
            }}
          />
          <motion.line
            x1="200" y1="180" x2="250" y2="200"
            stroke="#ffb347"
            strokeWidth="2.5"
            strokeOpacity="0.8"
            animate={{
              x2: [250, 240, 250],
              y2: [200, 210, 200]
            }}
            transition={{
              duration: 1.2,
              repeat: Infinity,
              repeatType: "reverse",
              delay: 0.3
            }}
          />
          
          {/* Legs */}
          <line x1="200" y1="250" x2="170" y2="300" stroke="#ffd78c" strokeWidth="2.5" strokeOpacity="0.8"/>
          <line x1="200" y1="250" x2="230" y2="300" stroke="#ffd78c" strokeWidth="2.5" strokeOpacity="0.8"/>
          
          {/* Laptop */}
          <rect x="120" y="220" width="160" height="100" rx="5" fill="none" stroke="#ffb347" strokeWidth="2.5" strokeOpacity="0.8"/>
          <rect x="130" y="230" width="140" height="80" fill="rgba(40, 28, 16, 0.3)" stroke="#ffb347" strokeWidth="1.5" strokeOpacity="0.8"/>
          
          {/* Code lines */}
          {[0, 1, 2, 3, 4].map((i) => (
            <motion.line
              key={i}
              x1="140" 
              y1={260 + i * 15} 
              x2={140 + Math.random() * 100} 
              y2={260 + i * 15}
              stroke="#ffd78c"
              strokeWidth="2"
              strokeOpacity="0.8"
              initial={{ opacity: 0 }}
              animate={{ opacity: [0, 0.8, 0] }}
              transition={{
                duration: 2,
                repeat: Infinity,
                delay: i * 0.3
              }}
            />
          ))}
        </motion.svg>
      </Box>
  );
};

export default CodingAnimation;