import React, { useRef, useState } from 'react';
import { Box, Typography, Button, useMediaQuery, useTheme } from '@mui/material';
import { Link } from 'react-router-dom';
import { motion, useInView, AnimatePresence } from 'framer-motion';
import { LightBulbIcon, CodeBracketIcon, BookOpenIcon } from '@heroicons/react/24/outline';
import CodingAnimation from './CodingAnimation';
import { blogPosts } from './AboutMe';
import { ArrowLeftIcon } from '@heroicons/react/24/outline';

const Home: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const homeRef = useRef<HTMLDivElement>(null);
  const writingsRef = useRef<HTMLDivElement>(null);
  const isHeroInView = useInView(homeRef, { once: true, amount: 0.1 });
  const isWritingsInView = useInView(writingsRef, { once: true, amount: 0.1 });
  const [hasAnimated, setHasAnimated] = useState(false);

  React.useEffect(() => {
    if (isWritingsInView && !hasAnimated) {
      setHasAnimated(true);
    }
  }, [isWritingsInView, hasAnimated]);
  
  const scrollToWritings = () => {
    writingsRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <Box sx={{
      minHeight: '100vh',
      background: 'rgba(40, 28, 16, 1)',
      position: 'relative',
      overflow: 'hidden',
      '&::before': {
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        background: `repeating-linear-gradient(
          45deg,
          rgba(255, 215, 140, 0.1),
          rgba(255, 215, 140, 0.1) 2px,
          transparent 2px,
          transparent 4px
        )`,
        pointerEvents: 'none'
      }
    }}>
      {/* Hero Section */}
      <Box
        py={isMobile ? 6 : 10}
        px={2}
        sx={{
          display: 'flex',
          flexDirection: isMobile ? 'column' : 'row',
          alignItems: 'center',
          justifyContent: 'center',
          gap: isMobile ? 4 : 6,
          minHeight: '100vh'
        }}
        id="home"
        ref={homeRef}
      >
        {!isMobile && <CodingAnimation />}
        
        {!isMobile && [...Array(3)].map((_, i) => (
          <motion.div 
            key={i}
            style={{ position: 'absolute', top: `${10 + i * 30}%`, left: `${5 + i * 10}%` }}
            animate={{ y: [0, -20, 0], rotate: [0, 15, -15, 0] }}
            transition={{ duration: 4 + i, repeat: Infinity, delay: i * 0.5 }}
          >
            <LightBulbIcon className="h-16 w-16 text-amber-400 opacity-20" />
          </motion.div>
        ))}

        <Box
          component={motion.div}
          initial={{ opacity: 0, x: isMobile ? 0 : -50 }}
          animate={isHeroInView ? { opacity: 1, x: 0 } : {}}
          transition={{ duration: 0.8, delay: 0.2 }}
          sx={{
            flex: 1,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            width: isMobile ? '70%' : 'auto',
            minWidth: { md: 300 },
            maxWidth: { md: 400 },
            zIndex: 2,
            mb: isMobile ? 2 : 0
          }}
        >
          <Box sx={{ 
            position: 'relative',
            width: '100%',
            maxWidth: isMobile ? 250 : 350,
            borderRadius: '8px',
            overflow: 'hidden',
            border: '4px solid rgba(255, 215, 140, 0.2)',
            boxShadow: '0 10px 30px rgba(0, 0, 0, 0.3)',
            '&:hover': {
              '&::before': {
                opacity: 0.3
              }
            },
            '&::before': {
              content: '""',
              position: 'absolute',
              inset: 0,
              background: 'linear-gradient(45deg, rgba(255, 179, 71, 0.5), rgba(255, 215, 140, 0.5))',
              opacity: 0,
              transition: 'opacity 0.3s ease',
              zIndex: 1
            }
          }}>
            <motion.img
              src={`${process.env.PUBLIC_URL}/IMG_3370.jpg`}
              alt="Dhruvil Kotecha"
              style={{
                width: '100%',
                height: '100%',
                objectFit: 'cover',
                display: 'block'
              }}
              whileHover={{ scale: 1.05 }}
              transition={{ type: 'spring', stiffness: 300 }}
            />
          </Box>
        </Box>
        
        <Box
          component={motion.div}
          initial={{ opacity: 0, y: isMobile ? 30 : 50 }}
          animate={isHeroInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.8 }}
          sx={{
            flex: 1,
            maxWidth: isMobile ? '90%' : 600,
            textAlign: isMobile ? 'center' : 'left',
            zIndex: 2
          }}
        >
          <Typography
            variant="h1"
            gutterBottom
            sx={{
              fontWeight: 900,
              letterSpacing: '-0.05em',
              mb: 2,
              color: '#ffd78c',
              textShadow: '0 2px 4px rgba(0,0,0,0.3)',
              fontSize: isMobile ? '2.25rem' : '3.5rem'
            }}
          >
            Dhruvil Kotecha
            <motion.div
              initial={{ scaleX: 0 }}
              animate={isHeroInView ? { scaleX: 1 } : {}}
              transition={{ delay: 0.3, duration: 0.8, ease: "easeOut" }}
              style={{
                height: '4px',
                background: 'linear-gradient(90deg, #ffb347, #ffd78c)',
                borderRadius: '2px',
                marginTop: '8px',
                transformOrigin: isMobile ? 'center' : 'left center'
              }}
            />
          </Typography>

          <Typography
            variant="h6"
            sx={{
              mb: 3,
              color: 'rgba(255, 215, 140, 0.9)',
              display: 'flex',
              flexWrap: 'wrap',
              alignItems: 'center',
              gap: 1,
              justifyContent: isMobile ? 'center' : 'flex-start',
              fontSize: isMobile ? '1rem' : '1.25rem'
            }}
          >
            Baking Enthusiast • Software Developer • Machine Learning Enthusiast • Lifelong Learner
          </Typography>

          <Typography
            variant="body1"
            sx={{ 
              mb: 4, 
              color: 'rgba(255, 215, 140, 0.8)', 
              fontSize: isMobile ? '1rem' : '1.1rem',
              lineHeight: 1.6
            }}
          >
            Passionate about building intelligent systems and crafting delightful user experiences. 
            Whether I'm developing efficient APIs, analyzing complex datasets, or baking artisan 
            sourdough—I thrive at the intersection of precision and creativity. Just like my code, 
            my bakes are well-structured, carefully proofed, and made with ❤️.
          </Typography>

          <Box 
            component={motion.div} 
            whileHover={{ scale: 1.05 }} 
            sx={{ 
              display: 'flex', 
              gap: 2, 
              flexWrap: 'wrap',
              justifyContent: isMobile ? 'center' : 'flex-start'
            }}
          >
            <Button
              variant="contained"
              size={isMobile ? 'medium' : 'large'}
              sx={{
                background: 'linear-gradient(90deg, rgba(255, 179, 71, 0.7), rgba(255, 215, 140, 0.7))',
                color: 'rgba(40, 28, 16, 0.9)',
                fontWeight: 700,
                borderRadius: '999px',
                px: isMobile ? 4 : 5,
                py: 1,
                textTransform: 'none',
                boxShadow: '0 6px 16px rgba(0,0,0,0.2)',
                '&:hover': {
                  background: 'linear-gradient(90deg, rgba(255, 179, 71, 0.9), rgba(255, 215, 140, 0.9))',
                },
              }}
              component={Link}
              to="/projects"
            >
              Explore My Work
            </Button>
            <Button
              variant="outlined"
              size={isMobile ? 'medium' : 'large'}
              sx={{
                borderColor: '#ffb347',
                color: '#ffd78c',
                fontWeight: 700,
                borderRadius: '999px',
                px: isMobile ? 4 : 5,
                py: 1,
                textTransform: 'none',
                '&:hover': {
                  background: 'rgba(255, 179, 71, 0.1)',
                  borderColor: '#ffd78c'
                },
              }}
              component={Link}
              to="/aboutme"
            >
              Know More About Me
            </Button>
          </Box>
          
          {!isMobile && (
            <Button
              onClick={scrollToWritings}
              startIcon={<ArrowLeftIcon className="h-5 w-5" />}
              sx={{
                color: '#ffd78c',
                textTransform: 'none',
                fontWeight: 600,
                mb: 4,
                '&:hover': {
                  color: '#ffb347',
                  background: 'rgba(255, 179, 71, 0.1)'
                }
              }}
            >
              Check my blogs
            </Button>
          )}
        </Box>
      </Box>

      {/* Latest Writings Section */}
      <Box
        ref={writingsRef}
        sx={{
          py: isMobile ? 6 : 10,
          px: isMobile ? 2 : 6,
          background: `
            linear-gradient(
              45deg,
              rgba(50, 35, 20, 0.9),
              rgba(40, 28, 16, 0.9)
            )`,
          position: 'relative',
          zIndex: 2
        }}
      >
        <Box
          sx={{
            maxWidth: 1100,
            mx: 'auto',
            p: isMobile ? 3 : 6,
            borderRadius: 4,
            border: '2px solid rgba(255, 215, 140, 0.2)',
            boxShadow: '0 20px 40px rgba(0,0,0,0.3)',
            position: 'relative',
            '&::before': {
              content: '""',
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              border: '1px solid rgba(255, 215, 140, 0.1)',
              borderRadius: 4,
              pointerEvents: 'none'
            }
          }}
        >
          <Typography
            variant="h3"
            sx={{
              fontWeight: 800,
              fontSize: isMobile ? '1.75rem' : '2.5rem',
              mb: 4,
              textAlign: 'center',
              color: '#ffd78c',
              textShadow: '0 2px 4px rgba(0,0,0,0.3)',
              position: 'relative',
            }}
          >
            Latest Writings
            <motion.div
              initial={{ scaleX: 0 }}
              animate={isWritingsInView ? { scaleX: 1 } : {}}
              transition={{ duration: 0.8, ease: "easeOut" }}
              style={{
                height: '3px',
                background: 'linear-gradient(90deg, #ffb347, #ffd78c)',
                borderRadius: '2px',
                marginTop: '8px',
                transformOrigin: 'left center',
                width: '100px',
                margin: '8px auto 0',
              }}
            />
          </Typography>

          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
            <AnimatePresence>
              {blogPosts.map((blog, index) => (
                <motion.div
                  key={blog.id}
                  initial={{ opacity: 0, y: 20 }}
                  animate={hasAnimated ? { opacity: 1, y: 0 } : {}}
                  transition={{ delay: hasAnimated ? index * 0.2 : 0, duration: 0.6 }}
                >
                  <Box
                    component={Link}
                    to={`/blog/${blog.id}`}
                    sx={{
                      display: 'flex',
                      alignItems: 'center',
                      gap: isMobile ? 2 : 3,
                      p: isMobile ? 2 : 3,
                      borderRadius: 4,
                      bgcolor: 'rgba(255, 255, 255, 0.05)',
                      border: '1px solid rgba(255, 215, 140, 0.2)',
                      textDecoration: 'none',
                      transition: 'all 0.3s ease',
                      '&:hover': {
                        bgcolor: 'rgba(255, 179, 71, 0.15)',
                        borderColor: '#ffb347',
                        transform: 'translateY(-3px)',
                        boxShadow: '0 10px 20px rgba(0,0,0,0.2)'
                      }
                    }}
                  >
                    <Box
                      sx={{
                        fontSize: '2rem',
                        width: isMobile ? '50px' : '60px',
                        height: isMobile ? '50px' : '60px',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        borderRadius: '50%',
                        bgcolor: 'rgba(255, 179, 71, 0.2)',
                        flexShrink: 0
                      }}
                    >
                      {blog.icon}
                    </Box>
                    <Box>
                      <Typography 
                        variant="h5" 
                        sx={{ 
                          color: '#ffd78c', 
                          fontWeight: 600,
                          mb: 1,
                          fontSize: isMobile ? '1.1rem' : '1.5rem'
                        }}
                      >
                        {blog.title}
                      </Typography>
                      <Typography 
                        sx={{ 
                          color: '#ffe4b5',
                          fontSize: isMobile ? '0.9rem' : '0.95rem'
                        }}
                      >
                        {blog.excerpt}
                      </Typography>
                    </Box>
                  </Box>
                </motion.div>
              ))}
            </AnimatePresence>
          </Box>
        </Box>
      </Box>

      {/* Floating decorative elements - hidden on mobile */}
      {!isMobile && (
        <>
          <motion.div 
            style={{ position: 'absolute', bottom: '10%', right: '5%', zIndex: 1 }}
            animate={{ rotate: 360 }}
            transition={{ duration: 20, repeat: Infinity, ease: "linear" }}
          >
            <CodeBracketIcon className="h-24 w-24 text-amber-400 opacity-10" />
          </motion.div>

          {/* Animated book icons for writings section */}
          {[...Array(3)].map((_, i) => (
            <motion.div 
              key={`book-${i}`}
              style={{ position: 'absolute', top: `${60 + i * 15}%`, left: `${80 + i * 5}%`, zIndex: 1 }}
              animate={{ y: [0, -15, 0], rotate: [0, 10, -10, 0] }}
              transition={{ duration: 5 + i, repeat: Infinity, delay: i * 0.5 }}
            >
              <BookOpenIcon className="h-12 w-12 text-amber-400 opacity-20" />
            </motion.div>
          ))}
        </>
      )}
    </Box>
  );
};

export default Home;